import { Component, h, Host, Prop, State } from '@stencil/core';

@Component({
    tag: 'ks-page-account',
    styleUrl: 'account.css'
})
export class PageAccount {
    @Prop() heading: string = "Panel Klienta";
    @Prop() paragraph: string = "Witaj z powrotem, {name}! Zarządzaj swoim kontem, zamówieniami i preferencjami.";

    @Prop() email: string = "test@test.pl";
    @Prop() phone: string = "+48 999 999 999";
    @Prop() firstname: string = "Imię";
    @Prop() lastname: string = "Nazwisko";
    @Prop() address: string = "Adres 1/1 50-500 Miasto Kraj";

    @Prop() pointsHeading: string = "Punkty lojalnościowe";
    @Prop() pointsSumMessage: string = "Suma zgromadzonych punktów:";
    @Prop() pointsCount: number = 1600;
    @Prop() pointsHistoryJson: string = "";

    @Prop() returnHeading: string = "Reklamacje";
    @Prop() returnParagraph: string = "Jeśli coś poszło nie tak, daj nam znać — chcemy to naprawić. </br> Skontaktuj się z nami drogą mailową lub telefoniczną pod numerem <b>727 777 432</b>. <br/> Aby złożyć reklamację, wypełnij odpowiedni formularz i prześlij go na adres: <b>sklep@kuchniasklep.pl</b>. <br/> Dołożymy wszelkich starań, aby odpowiedzieć jak najszybciej.";
    
    @Prop() orderLink: string = "/zamowienia-szczegoly-zs-{order}.html";
    @Prop() ordersLink: string = "#";
    @Prop() productsLink: string = "#";
    @Prop() dataLink: string = "#";
    @Prop() passwordLink: string = "#";
    @Prop() logoutLink: string = "#";
    @Prop() returnLink: string = "#";
    @Prop() pointsLink: string = "#";
    
    @State() loading: boolean = false;

    @State() pointsHistory: PointsHistoryEntry[];
    componentWillLoad() {
        this.pointsHistory = this.pointsHistoryJson ? JSON.parse(this.pointsHistoryJson) : [];
    }

	render() {
		return <Host>
            <section class="container">
                <h1>{this.heading}</h1>
                <p>{this.paragraph.replace("{name}", this.firstname)}</p>

                <div class="buttons">
                    <ks-button rounded link={this.ordersLink} name="Zamówienia" />
                    <ks-button rounded link={this.productsLink} name="Produkty" />
                    <ks-button rounded link={this.dataLink} name="Moje dane" />
                    <ks-button rounded link={this.passwordLink} name="Zmiana hasła" />
                    <ks-button rounded secondary link={this.logoutLink} name="Wyloguj" />
                </div>

                <div class="address">
                    <h2>Aktualne dane</h2>
                    <div>Imię i nazwisko: <b>{this.firstname} {this.lastname}</b></div>
                    <div>Adres email: <b>{this.email}</b></div>
                    <div>Nr telefonu: <b>{this.phone}</b></div>

                    <div class="street">{this.address}</div>
                </div>

                <div class="points">
                    <h2>{this.pointsHeading}</h2>
                    <div class="sum">
                        <div>{this.pointsSumMessage}</div>
                        <div class="count">{this.pointsCount}</div>
                    </div>

                    <div class="history">
                        {this.pointsHistory.map(entry => 
                            <div class="entry">
                                <div class="info">
                                    <div class="amount">{entry.amount}</div>
                                    <div class="comment">{entry.comment}</div>
                                </div>
                                <div class="date">{entry.date}</div>
                                <div class="status">{entry.status}</div>
                                <div class="order"> {entry.order && 
                                    <ks-button round narrower
                                        link={this.orderLink.replace("{order}", entry.order)}
                                        name={`Zamówienie: ${entry.order}`}
                                    />
                                }</div>
                            </div>
                        )}
                    </div>

                    <ks-button rounded name="Informacje o programie" link={this.pointsLink} />
                </div>

                <div class="returns">
                    <h2>{this.returnHeading}</h2>
                    <p innerHTML={this.returnParagraph}></p>

                    <ks-button rounded name="Formularze i szczegóły" link={this.returnLink} />
                </div>
            </section>
        </Host>;
	}
}

interface PointsHistoryEntry {
    amount: number;
    comment: string;
    date: string;
    status: string;
    order?: string;
}