ks-page-account {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    
}

ks-page-account .container {
    max-width: 100%;
    width: 1000px;
    box-sizing: border-box;
    padding: 30px;
    margin: 30px 0px;
    background-color: #fff;
    border-radius: 10px;
}

ks-page-account .container h2 {
    margin-top: 40px;
    margin-bottom: 8px;
}

ks-page-account .buttons {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

ks-page-account .buttons > * {
    flex: 1 0 auto;
}

ks-page-account .address {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

ks-page-account .address b {
    font-size: 18px;
    font-family: var(--font-emphasis);
}

ks-page-account .address .street {
    margin-top: 5px;
}

ks-page-account .points .sum {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: -10px;
}

ks-page-account .points .count {
    font-size: 28px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

ks-page-account .points .history {
    display: flex;
    flex-direction: column;
    gap: 10px;

    margin: 20px 0px;
}

ks-page-account .points .history .entry {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 3px 10px;

    background-color: #f2f2f2;
    padding: 8px 20px;
    border-radius: 10px;
}

ks-page-account .points .history .entry .amount {
    flex: 0 0 60px;
    text-align: left;
    font-weight: 700;
    font-size: 18px;
    font-family: var(--font-emphasis);
}

ks-page-account .history .status { flex: 0 0 120px; }
ks-page-account .history .order { flex: 0 0 180px; }
ks-page-account .history .date { flex: 0 0 100px; }
ks-page-account .history .comment { flex: 0 1 auto; }

ks-page-account .history .info {
    flex: 1 1 260px;
    display: flex;
    flex-wrap: wrap;
    gap: 0px 10px;
    align-items: center;
}

@media (max-width: 1060px) {
    ks-page-account {
        background-color: #fff;
    }

    ks-page-account .container {
        padding: 40px 20px;
        margin: 0px;
    }
}

@media (max-width: 820px) {
    ks-page-account {
        text-align: center;
    }

    ks-page-account h2 {
        margin-top: 50px;
    }

    ks-page-account .buttons {
        margin-top: 30px;
    }

    ks-page-account .points .sum {
        display: block;
        text-align: center;
        margin-top: 0px;
    }

    ks-page-account .points .history .entry {
        justify-content: center;
    }

    ks-page-account .points .history .entry .info {
        flex: 1 1 100%;
        justify-content: center;
        text-align: center;
    }

    ks-page-account .points .history .entry .amount,
    ks-page-account .points .history .entry .status {
        flex: 0 0 auto;
    }

    ks-page-account .points .history .entry .order {
        display: none;
    }

    ks-page-account .points .history .entry .date {
        text-align: center;
    }
}